import { useEffect, useState } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import navIcon1 from '../assets/img/nav-icon1.svg';
import navIcon2 from '../assets/img/nav-icon2.svg';
import navIcon3 from '../assets/img/nav-icon3.svg';
import { HashLink } from 'react-router-hash-link';
import {BrowserRouter as Router} from "react-router-dom";

export const NavBar = () => {
  const [ activeLink, setActiveLink ] = useState('home');
  const [ scrolled, setScrolled ] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else{
        setScrolled(false);
      }
  }

  window.addEventListener("scroll", onScroll);

  return () => window.removeEventListener("scroll", onScroll);
}, [])

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  }

  return (
    <Router>
      <Navbar expand="lg" className={scrolled ? "scrolled" : ""}>
        <Container>
          <Navbar.Toggle aria-controls="basic-navbar-nav">
            <span className='navbar-toggler-icon'></span>
          </Navbar.Toggle>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#home"
                        className={activeLink === 'home' ? 'active navbar-link': 'navbar-link'}
                        onClick={() => onUpdateActiveLink('home')}
              >Home</Nav.Link>
              <Nav.Link href="#skills"
                        className={activeLink === 'skills' ? 'active navbar-link': 'navbar-link'}
                        onClick={() => onUpdateActiveLink('skills')}
              >Skills</Nav.Link>
              <Nav.Link href="#project"
                        className={activeLink === 'project' ? 'active navbar-link': 'navbar-link'}
                        onClick={() => onUpdateActiveLink('project')}
              >Projects</Nav.Link>
            
            </Nav>
            <span className='navbar-text'>
              <div className='social-icon'>
                <a href='https://www.linkedin.com/in/weidong-kong-jim/'><img src={navIcon1} alt=''/></a>
                <a href='https://www.facebook.com/weidong.kong/'><img src={navIcon2} alt=''/></a>
                <a href='https://www.instagram.com/wkongjim/'><img src={navIcon3} alt=''/></a>
              </div>
              <HashLink to="#contact">
                <button href="#contact" className='vvd'><span>Let's Connect!</span></button>
              </HashLink>
            </span>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </Router>
    
  )
}